.navBar {
    display: flex;
    justify-content: space-between;
    height: 8vh;
    box-shadow: 2px 2px 2px rgb(189, 170, 170);

    .left {
        display: flex;
        align-items: center;
        margin-left: 10px;

        .menu-icon {
            font-size: 3em;
            margin-right: 1em;
            display: flex;
            position: relative;
            cursor: pointer;

        }
    }

    .rigth {
        margin-right: 20px;
        padding-right: 20px;
        text-shadow: 2px 2px 20px rgb(12, 12, 12);
        align-self: center;
    }
}


.nav-ul{
    padding-left:0px
}


.nav-item {
    position: fixed;
    top: -100%;
    display: flex;
    width:auto;
    height: auto;
    background-color: transparent;
    z-index: 1000;
    transition: 1s ease-in-out;
}

.nav-item.active {
    width:auto;
    height: auto;
    left: 0%;
    top: 0px;
    transition: 1s ease-in-out;
    border: 1px black solid;
    background-color: black;
    border-radius: 0px;
}

.menu-item {
    height: 40px;
    list-style: none;
    margin: 10px;
    padding: 0px;
    width: 100%;
    font-size: 1em;
}

.menu-item a {
    text-decoration: none;
    color: rgb(175, 60, 60);
    font-weight: bolder;
    text-transform: uppercase;
    text-shadow: 2px 2px 15px #fff;

    padding: 10px;
    border-radius: 10px;

}

