.formularioContacto {
  margin-top: 0px;
  background: linear-gradient(to right, #434343 0%, #eb466b 100%);
  font-size: 12px;
  display:flex;
  justify-content:center;
  align-items:center;
}


.background {
  display: flex;
  min-height: 92vh;
}

.container {
  flex: 0 1 700px;
  margin: auto;
  padding: 10px;

}

.screen {
  position: relative;
  background: #3e3e3e;
  border-radius: 15px;
}

.screen:after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 20px;
  right: 20px;
  bottom: 0;
  border-radius: 15px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, .4);
  z-index: -1;
}

.screen-header {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  background: #4d4d4f;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

.screen-header-left {
  margin-right: auto;
}

.screen-header-button {
  display: inline-block;
  width: 8px;
  height: 8px;
  margin-right: 3px;
  border-radius: 8px;
  background: white;
}

.screen-header-button.close {
  background: #ed1c6f;
}

.screen-header-button.maximize {
  background: #e8e925;
}

.screen-header-button.minimize {
  background: #74c54f;
}

.screen-header-right {
  display: flex;
}

.screen-header-ellipsis {
  width: 3px;
  height: 3px;
  margin-left: 2px;
  border-radius: 8px;
  background: #999;
}

.screen-body {
  display: flex;
  
}

.screen-body-item {
  flex: 1;
  padding: 70px;
}

.screen-body-item.left {
  display: flex;
  flex-direction: column;
}

.app-title {
  display: flex;
  flex-direction: column;
  position: relative;
  color: #ea1d6f;
  font-size: 26px;
  text-shadow: 0.05em 0 0 #00fffc, -0.03em -0.04em 0 #fc00ff, 0.025em 0 0.4em 0 #fffc00;
  animation: glitch 1000ms infinite
}

.app-title:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  bottom: -10px;
  width: 25px;
  height: 4px;
  background: #ea1d6f;
}

.app-contact {
  display: flex;
  margin-top: auto;
  font-size: 20px;
  color: #888;
}

.app-contact img {
  width: 20px;
}

.app-form-group {
  margin-bottom: 15px;
  width: 300px;

}

.app-form-group.message {
  margin: 40px 0;
  
}

.app-form-group.buttons {
  margin-bottom: 0;
  text-align: right;
}

.app-form-control {
  width: 100%;
  padding: 10px 0;
  background: none;
  border: none;
  border-bottom: 1px solid #666;
  color: #ddd;
  font-size: 14px;
  text-transform: uppercase;
  outline: none;
  transition: border-color .2s;
}

.app-form-control::placeholder {
  color: #666;
}

.app-form-control:focus {
  border-bottom-color: #ddd;
}

.app-form-button {
  background: none;
  border: none;
  color: #ea1d6f;
  font-size: 14px;
  cursor: pointer;
  outline: none;

}

.app-form-button:hover {
  color: #b9134f;

}

.app-contact img {
  width: 20px;
  transition: width 2s;
}

.app-contact img:hover {
  width: 50px;
}



.dribbble {
  width: 20px;
  height: 20px;
  margin: 0 5px;
}

@keyframes glitch {
  0% {
    text-shadow: 0.05em 0 0 #00fffc, -0.03em -0.04em 0 #fc00ff,
      0.025em 0.04em 0 #fffc00;
  }

  15% {
    text-shadow: 0.05em 0 0 #00fffc, -0.03em -0.04em 0 #fc00ff,
      0.025em 0.04em 0 #fffc00;
  }


}







@media screen and (max-width: 780px) {


  .screen-body {
    display: flex;
    flex-direction: column;
    padding: 70px;
   
  }

  .app-title {
    margin-bottom: 2em;
  }

  .screen-body-item {
    padding: 0;
  }

  .app-form-group {
    margin-bottom: 15px;
    width: 100%;
    
  }
}

