section{
 position: sticky;
    top: 0;
}
.slider{
    width: 100%;
    height: 100vh;
   
}


.slides.active{
    width: 100%;
    height: 100%;
    padding: 0 1.2em;

        img{
            width: 100%;
            height: 90vh; 
}
}


.buttons{
    color: #fff;
    cursor: pointer;

       .leftBtn{
           position: absolute;
           top: 50%;
           right: 4%;
       } 
       .rightBtn{
           position: absolute;
           top: 50%;
           left: 4%;
       }

    }



.content{
    position: absolute;
    left: 30%;
    align-items: center;
    top: 60%;
    text-align: center;

        h1{
            color: white;
            font-size: 3em;
            text-shadow: 2px 0px 15px white;
            text-transform: uppercase;
        }

        button{
            background: transparent;
            border: 1px solid white;
            padding: 10px 20px;
            color: white;
            text-transform: uppercase;
            margin: 1em 1em;
            cursor: pointer;

        }


    button:hover{
        background: #fff;
        color: black;
        transform: scale(140%);
        transition: 1s ease-in-out;
    }
}


 a{
    text-decoration: none;
}






$breakpoint-cell: 650px;
@media (max-width: $breakpoint-cell) {
  .slider{
          height: 40vh;
  }  


 .slides.active
  { 
      img{
       width: 100%;
       height: 40vh; 
    }

   
}

.buttons{
    .leftBtn{       
        top: 20%;
        right: 10%;
    } 

      .rightBtn{
        position: absolute;
        top: 20%;
        left: 10%;
       }
}

.content{
    left: 16%;
    top: 62%;

        h1{
        color: white;
        font-size: 1em;
        }
} 

}

@media (min-width: 800px) and (max-width: 950px){
    .slider{
        height: 40vh;
  } 
   .slides.active
  { 
      img{
       height: 40vh; 
    }

   
}

  
}